import React from 'react';
import { connect } from 'dva';
import styles from './myorder.css';
import { routerRedux } from 'dva/router';
import MyorderList from '../components/myorder/Myorder';
import Search from '../components/myorder/search';

function Myorder({dispatch,location,myorder}){
  const {loading, list, pagination} = myorder
  const { field, keyword } = location.query
  const MyorderListProps = {
    dataSource: list,
    loading,
    pagination: pagination,
    onPageChange (page) {
      const { query, pathname } = location
      dispatch(routerRedux.push({//换页
        pathname: pathname,
        query: {
          ...query,
          page: page.current,
          pageSize: page.pageSize
        }
      }))
    },
    onLookItem (itemId) {//查看详情
      dispatch({
        type: 'myorder/details',
        payload: {
          modalType:'detail',
          itemId: itemId,
          payload:location.query
        }
      })
    }
  }

  const myorderSearchProps = {
    field,
    keyword,
    onSearch (fieldsValue) {
      fieldsValue.keyword.length ? dispatch(routerRedux.push({
          pathname: '/admin/myorder',
          query: {
            field: fieldsValue.field,
            keyword: fieldsValue.keyword
          }
        })) : dispatch(routerRedux.push({
          pathname: '/admin/myorder',
        }))
    }
  }
  return (
    <div className={styles.normal}>
      <Search {...myorderSearchProps}/>
      <MyorderList {...MyorderListProps}　detailHref={'myorderdetails/'}/>
    </div>
  );
}
function mapStateToProps({myorder}) {
  return {myorder};
}
export default connect(mapStateToProps)(Myorder);



